import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
	
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			msgList: [],
			length: 10,
			page: 1,
			// 定义上拉加载更多变量
			loadingText: '加载中...',
			loadingType: 'more', //定义加载方式 more---contentdown  loading---contentrefresh noMore---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getMsgList();
	},
	onPullDownRefresh: function() {
		console.log('refresh，下拉刷新');
		this.page = 1;
		this.loadingType = 'more';
		this.msgList = [];
		this.getMsgList();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReachBottom: function() {
		console.log("上拉加载")
		this.getMsgList();
	},
	methods: {
		getMsgList() {
			let _this = this;
			if (_this.loadingType != 'more') {
				return ;
			}
			_this.loadingType = 'loading';
			uni.showNavigationBarLoading(); //显示加载动画
			_this.$minApi.uniapp_post(_this.$base_api.getMsgList, {
				'mallId': _this.$base_api.MALL_ID,
				page: _this.page,
				length: _this.length
			}).then(res => {
				if (res.success) {
					var tempData = res.data;
					if (res.data.length == 0) { //没有数据
						_this.loadingType = 'noMore';
						uni.hideNavigationBarLoading(); //关闭加载动画
						return;
					}
					_this.page++; // 每触底一次 page + 1
					tempData.forEach(msg => {
						msg.createTime = _this.formatDate(msg.createTime)
					})
					_this.loadingType = tempData.length < _this.length ? 'noMore' : 'more';
					console.log("_this.loadingType",_this.loadingType);
					_this.msgList = _this.msgList.concat(tempData); //将数据拼接在一起
					uni.hideNavigationBarLoading(); //关闭加载动画
				} else {
					console.log("查询失败")
				}
			});
		},
	}
}
